import * as React from 'react';
import logo from './logo.svg';
import './App.css';
import {
  BrowserRouter as Router,
  Route,
//  Link
} from 'react-router-dom';

import Result from './components/Result';


        
function ResultList()
{
  return <div>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
        <Result LOC="Oslo%2C+Norway" SRC="GDN" DST="OSL"/>
  </div>
}

function Error(props)
{
//console.log(props);
return <h1>{props.match.params.query} - 404 NOT FOUND</h1>
}


class App extends React.Component {
        
  render() {
                          
    return (
            <Router>
      <div className="App">
          
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Welcome to React</h2>
        </div><br/>
        
          <Route exact path={process.env.PUBLIC_URL + "/"} component={ResultList} />
          <Route path={process.env.PUBLIC_URL + "/:query"} component={Error} />


        </div>
        </Router>          
    );
  }
}

export default App;



