class common
{

constructor()
{
  this.wdays = ['Pon','Wto','Śro','Czw','Pią','Sob','Nie'];
}

getPriceBG($price:number): string
{
    let $bc = 'danger';
    if ($price <= 80) $bc = 'success';
    else if ($price <= 150) $bc = 'info';
    else if ($price <= 300) $bc = 'warning';
    return $bc;
}
};

export default new common();




